




















































































































import * as Typ from "../../types/index";
import {
  mdiAccount,
  mdiPencil,
  mdiShareVariant,
  mdiDelete,
  mdiAppleKeyboardControl,
  mdiContentSaveEdit,
  mdiFileRestoreOutline,
  mdiViewColumn,
  mdiViewComfy,
} from "@mdi/js";

import Vue from "vue";

export default Vue.extend({
  name: "FilterHeader",

  props: {
    sortItems: {
      type: Array as () => Array<Typ.ISortItem>,
      default() {
        return new Array<Typ.ISortItem>();
      },
    },
    search: {
      type: String,
    },
    sortDesc: {
      type: Boolean,
    },
    sortBy: {
      type: String,
    },
    lblSearch: {
      type: String,
      default: "Search tool",
    },
    lblSort: {
      type: String,
      default: "Sort by criteria",
    },
    showSort: {
      type: Boolean,
      default: true,
    },
    showViewSwitch: {
      type: Boolean,
      default: false,
    },
    currentView: {
      type: String,
      default: "DataTable",
    },
  },

  data() {
    return {
      hsortItems: this.sortItems,
      hsearch: this.search,
      hsortDesc: this.sortDesc,
      hsortBy: this.sortBy,

      icons: {
        mdiAccount,
        mdiPencil,
        mdiShareVariant,
        mdiDelete,
        mdiAppleKeyboardControl,
        mdiContentSaveEdit,
        mdiFileRestoreOutline,
        mdiViewColumn,
        mdiViewComfy,
      },
      Typ,
    };
  },

  methods: {
    viewChanged(newVal: string) {
      this.$emit("viewChanged", newVal);
    },
  },

  //WATCH
  watch: {
    search: function (newVal) {
      this.hsearch = newVal;
    },
    sortDesc: function (newVal) {
      this.hsortDesc = newVal;
    },
    sortBy: function (newVal) {
      this.hsortBy = newVal;
    },
    hsearch: function (newVal) {
      this.$emit("searchChanged", newVal);
    },
    hsortDesc: function (newVal) {
      this.$emit("sortDescChanged", newVal);
    },
    hsortBy: function (newVal) {
      this.$emit("sortByChanged", newVal);
    },
  },
});
